import React, { createContext, useState, useEffect } from 'react';

// Crear el contexto de autenticacin
export const AuthContext = createContext();

export const AuthProvider = ({ children }) => {
  const [user, setUser] = useState(null);

  // Cargar el usuario desde localStorage cuando la aplicacin se monta
  useEffect(() => {
    const storedUser = localStorage.getItem('user');
    
    // Verificar si el valor de storedUser es vlido antes de intentar parsearlo
    if (storedUser && storedUser !== "undefined") {
      try {
        setUser(JSON.parse(storedUser));
      } catch (error) {
        console.error("Error parsing JSON from localStorage", error);
        setUser(null); // Limpiar el estado del usuario si hay un error al parsear
      }
    }
  }, []);

  // Funcin para iniciar sesin
  const login = (userData) => {
    setUser(userData);
    localStorage.setItem('user', JSON.stringify(userData));
  };

  // Funcin para cerrar sesin
  const logout = () => {
    setUser(null);
    localStorage.removeItem('user');
  };

  return (
    <AuthContext.Provider value={{ user, login, logout }}>
      {children}
    </AuthContext.Provider>
  );
};
